import * as Marzipano from './lib/marzipano.js';
import * as Bowser from './lib/bowser.min.js';
import * as Screenfull from './lib/screenfull.min.js';
import scenesData from '../data/scenes.json';
import stairsData from '../data/stairs.json';
import * as utils from './utils.js';

export function createScenes() {
    let settings = {
        "name": "JRES 2024 - Visite 360",
        "mouseViewMode": "drag",
        "autorotateEnabled": false,
        "fullscreenButton": false,
        "viewControlButtons": false,
        "firstScene": "img-16"

    }
    let panoElement = document.querySelector('#pano');
    let viewerOpts = { controls: { mouseViewMode: settings.mouseViewMode } };

    // Initialize viewer.
    var viewer = new Marzipano.Viewer(panoElement, viewerOpts);

    function stopTouchAndScrollEventPropagation(element) {
        let eventList = ['touchstart', 'touchmove', 'touchend', 'touchcancel',
            'wheel', 'mousewheel'];
        eventList.forEach((event) => {
            element.addEventListener(event, function (e) {
                e.stopPropagation();
            });
        });
    }

    function getNavHotspotElement(selfSceneData, targets, prefix = false) {
        let element = document.createElement('div');
        element.classList.add('nav-target-cont', 'd-none', 'flex-column');
        for (let target in targets) {
            let scene = document.createElement('div');
            scene.classList.add('nav-target');
            if (targets[target] in scenesData) {
                let t = "";
                if (prefix) {
                    t += "[" + utils.levels2[scenesData[targets[target]].level] + "] ";

                }
                t += scenesData[targets[target]].name;
                scene.appendChild(document.createTextNode(t));

            } else {
                console.log("Erreur: la target " + targets[target] + " n'existe pas dans les scenes")
            }

            if (targets.length == 1) { scene.classList.add("mono"); }

            scene.addEventListener('click', function () {
                utils.switchScene(mScenesDict, [targets[target]], selfSceneData.id);
            });

            element.appendChild(scene);
        }

        return element;
    }

    function createNavHotspot(sceneData, hotspot) {
        let type = hotspot.type;
        let element = document.createElement('div');
        element.classList.add('hotspot', type + '-hotspot');

        let navFlexBox = document.createElement('div');
        navFlexBox.classList.add('nav-flex-cont', 'd-flex')

        // nav icon
        let navIconDiv = document.createElement('div');
        navIconDiv.classList.add('nav-icon-cont', 'd-flex', 'justify-content-center', 'align-items-center')

        let navIcon = document.createElement('i');
        if (hotspot.type == "nav") {
            navIcon.classList.add('d-flex', 'bi', 'bi-caret-up-fill');
        }
        if (hotspot.type == "stairs") {
            navIcon.classList.add('d-flex', 'fa-solid', 'fa-stairs');
        }

        navIconDiv.appendChild(navIcon);

        // nav targets
        let navTargetsDiv = {};
        if (hotspot.type == "nav") {
            navTargetsDiv = getNavHotspotElement(sceneData, hotspot.target);
        }
        if (hotspot.type == "stairs") {
            navTargetsDiv = getNavHotspotElement(sceneData, stairsData[hotspot.target].target.filter(t => t != sceneData.id), true);
        }

        navFlexBox.appendChild(navIconDiv);
        navFlexBox.appendChild(navTargetsDiv);
        element.appendChild(navFlexBox);

        stopTouchAndScrollEventPropagation(element);


        let t = element.childNodes[0].childNodes[1];
        element.addEventListener("mouseenter", function (e) {
            element.classList.add("hover");
        });
        element.addEventListener("mouseleave", function (e) {
            element.classList.remove("hover");
            t.classList.remove('d-flex');
            t.classList.add('d-none');
        });

        element.addEventListener("transitionend", function (e) {
            if (element.classList.contains('hover')) {
                t.classList.add('d-flex');
                t.classList.remove('d-none');
            }
        });

        return element;
    }

    function createIconHotspot(sceneData, hotspot) {
        let element = document.createElement('div');
        element.classList.add('hotspot', 'icon-hotspot');

        let iconFlexBox = document.createElement('div');
        iconFlexBox.classList.add('icon-flex-cont', 'd-flex', 'justify-content-center', 'align-items-center')

        let icon = document.createElement('i');
        icon.classList.add('d-flex');
        icon.classList.add(...(hotspot.icon.split(" ")));

        iconFlexBox.appendChild(icon);
        element.appendChild(iconFlexBox);
        stopTouchAndScrollEventPropagation(element);

        return element;
    }

    function createScenes() {
        let tilesPath = "assets/tiles/"
        let levels = [
            { "tileSize": 256, "size": 256, "fallbackOnly": true },
            { "tileSize": 512, "size": 512 },
            { "tileSize": 512, "size": 1024 },
            { "tileSize": 512, "size": 2048 }
        ];
        let geometry = new Marzipano.CubeGeometry(levels);
        let faceSize = 1520;
        let limiter = Marzipano.RectilinearView.limit.traditional(faceSize, 100 * Math.PI / 180, 120 * Math.PI / 180);


        let scenes = {};
        for (let id in scenesData) {
            let sceneData = scenesData[id];
            let imageSource = Marzipano.ImageUrlSource.fromString(
                tilesPath + id + "/{z}/{f}/{y}/{x}.jpg",
                { cubeMapPreviewUrl: tilesPath + id + "/preview.jpg" }
            );
            let mView = new Marzipano.RectilinearView(90 * Math.PI / 180, limiter);
            let scene = viewer.createScene({
                source: imageSource,
                geometry: geometry,
                view: mView,
                pinFirstLevel: true
            });
            sceneData.hotspots.forEach(function (h) {
                try {
                    let element = {};
                    switch (h.type) {
                        case "nav":
                        case "stairs":
                            element = createNavHotspot(sceneData, h);
                            break;
                        case "icon":
                            element = createIconHotspot(sceneData, h);
                        default:
                            break;
                    }
                    scene.hotspotContainer().createHotspot(
                        element,
                        { yaw: h.yaw * Math.PI / 180, pitch: h.pitch * Math.PI / 180 }
                    );
                } catch (e) {
                    console.log(e);
                }
            });

            scenes[id] = { "data": sceneData, "scene": scene, "view": mView };
        }
        return scenes;
    }

    // setup axio map area coords depending of media
    addEventListener("change", (event) => {
        for (let level in utils.levels) {
            if (window.matchMedia("(max-width: 768px)").matches) {
                document.querySelector("#axio-area-" + level).coords = utils.levels[level]["coords-mb"];
            } else {
                document.querySelector("#axio-area-" + level).coords = utils.levels[level]["coords-dt"];
            }
        }
    });

    var mScenesDict = createScenes();
    const tooltipTriggerList = document.querySelectorAll('[data-bs-toggle="tooltip"]');
    const tooltipList = [...tooltipTriggerList].map(tooltipTriggerEl => new bootstrap.Tooltip(tooltipTriggerEl));

    //console.log(mScenesDict);
    utils.switchScene(mScenesDict, settings.firstScene);
    return mScenesDict;
}
