export function getInitialViewParameters(targetData, sourceId) {
    let viewParameters = null;

    if (sourceId) {
        targetData.hotspots.forEach(h => {
            if (h.type == "nav") {
                h.target.forEach(t => {
                    if (t == sourceId && viewParameters == null) {
                        viewParameters = { "pitch": h.pitch * Math.PI / -180, "yaw": ((h.yaw + 180) % 360) * Math.PI / 180 };
                    }
                })
            } else if (h.type == "stairs") {
                stairsData[h.target].target.forEach(t => {
                    if (t == sourceId && viewParameters == null) {
                        viewParameters = { "pitch": h.pitch * Math.PI / -180, "yaw": ((h.yaw + 180) % 360) * Math.PI / 180 };
                    }
                });
            }
        });
    } else if (targetData.initialViewParameters) {
        viewParameters = targetData.initialViewParameters;
    }

    if (viewParameters == null) {
        viewParameters = { "pitch": 0, "yaw": 0, };
    }

    return viewParameters
}

function updateSceneName(scene) {
    let titleDiv = document.querySelector("#ttb-scene-title");
    titleDiv.textContent = "";

    let titleIcon = document.createElement("i");
    titleIcon.classList.add(...(rolesAll[scene.data["map-role"]]["icon"].split(" ")), "pe-1");
    titleIcon.setAttribute("data-bs-toggle", "tooltip");
    titleIcon.setAttribute("data-bs-title", rolesAll[scene.data["map-role"]]["tooltip"]);
    titleIcon.setAttribute("data-bs-placement", "bottom");

    let titleName = document.createElement("p");
    titleName.appendChild(document.createTextNode(scene.data.name));

    titleDiv.appendChild(titleIcon);
    titleDiv.appendChild(titleName);
    document.querySelector("#current-scene").setAttribute("data-id", scene.data.id)
}

/**
 * Sets a CSS class on a subset of an element list.
 * Exemples:
 * - to set the 'hover' class to one scene element in all scenes,
 *     setClassInList("#ar-scene-img-16"), ".scene", "hover");
 * - to set the 'active' class to one axio masks and the 'inactive' class to all the others,
 *     setClassInList("#axio-mask-lm2", ".axio-mask", "active", "inactive");
 * @param {string} itemSelector CSS selector of the item(s) on wich we add a class.
 * @param {string} listSelector CSS selector of the items on which we remove a class.
 * @param {string} itemToAdd class to add on the selected item(s) and to remove on the others.
 * @param {string} itemToRemove class to remove from the selected item(s) and to add back on the others.
 */
export function setClassInList(itemSelector, listSelector, itemToAdd = null, itemToRemove = null) {
    document.querySelectorAll(listSelector).forEach((e) => {
        if (e.matches(itemSelector)) {
            if (itemToAdd != null) { e.classList.add(itemToAdd); }
            if (itemToRemove != null) { e.classList.remove(itemToRemove) }
        } else {
            if (itemToAdd != null) { e.classList.remove(itemToAdd); }
            if (itemToRemove != null) { e.classList.add(itemToRemove); }
        }
    });
}

function setActiveScene(id) {
    setClassInList("#ar-scene-" + id + ", #ls-scene-" + id + ", #mr-scene-" + id, ".scene", "active");
    setClassInList("#ar-map-pointer-" + id + ", #ls-map-pointer-" + id, ".map-pointer", "active");
}

function setActiveLevel(level) {
    setClassInList("#axio-nav-" + level, ".axio-nav", "active");
    setClassInList("#axio-mask-" + level, ".axio-mask", "active", "inactive");
    setClassInList("#axio-scenes-" + level, ".axio-scenes", "active", "inactive");
    setClassInList("#map-scenes-" + level, ".map-scenes", "active", "inactive");
    setClassInList("#map-image-div-" + level, ".map-image-div", "active", "inactive");
}

export function switchScene(mScenes, targetId, sourceId = null) {
    let mSceneTarget = mScenes[targetId];

    setActiveScene(mSceneTarget.data.id);
    setActiveLevel(mSceneTarget.data.level);
    // set initial view parameters
    // if we have a source scene, we invert the camera from the hotspot leading to the room we come from
    let initialViewParameters = getInitialViewParameters(mSceneTarget.data, sourceId);
    //stopAutorotate();
    mSceneTarget.view.setParameters(initialViewParameters);
    mSceneTarget.scene.switchTo();
    //startAutorotate();
    updateSceneName(mSceneTarget);
    //updateSceneList(scene);
}