import * as u from './utils.js';
import scenesData from '../data/scenes.json';

export function print() {
    const scenesByRole = getScenesByAttribute(scenesData, "map-role");
    const scenesByLevel = getScenesByAttribute(scenesData, "level");

    var sceneCounter = 1; // global scene counter to number the scene elements.
    var levelLabel = true; // global flag to display level in scenes elements in the all rooms menu.

    /**
     * Creates a dictionary {attribute_value : data{}} from a scenes dictionary.
     * It does not sort the list.
     * @param scenesDict input dict
     * @param attribute attribute to filter on
     * @returns filtered dictionary
     */
    function getScenesByAttribute(scenesDict, attribute) {
        let sceneList = {};
        for (let scene in scenesDict) {
            let sceneAttribute = scenesDict[scene][attribute];
            if (!(sceneAttribute in sceneList)) {
                sceneList[sceneAttribute] = {}
            }
            sceneList[sceneAttribute][scene] = scenesDict[scene];
        }

        return sceneList;
    }

    /**
     * Returns a scene <a> element displayed in the content menu.
     * @param {string} prefix menu prefix (ar/ls)
     * @param {object} scene scene object
     * @param {number} num element counter
     * @returns <a> element
     */
    function getSceneElement(prefix, scene, num) {
        let element = document.createElement("a");
        element.id = prefix + "-scene-" + scene["id"];
        element.classList.add("scene");
        element.setAttribute("data-id", scene["id"]);
        element.setAttribute("data-lvl", scene["level"]);
        element.setAttribute("href", "javascript:void(0)");

        let sceneId = document.createElement("div");
        sceneId.classList.add("scene-id");
        sceneId.style.backgroundColor = "var(--" + scene["map-role"] + "-color)";
        sceneId.appendChild(document.createTextNode(num));

        let sceneLabel = document.createElement("div");
        sceneLabel.classList.add("scene-label");
        sceneLabel.appendChild(
            document.createTextNode((levelLabel ? "[" + u.levels[scene["level"]]["label"] + "] " : "") + scene["name"])
        );

        element.appendChild(sceneId);
        element.appendChild(sceneLabel);

        return element;
    }

    /**
     * Returns the title <div> for a specific map role.
     * @param {string} role map role
     * @returns <div> element
     */
    function getSceneTitleElement(role) {
        let element = document.createElement("div");
        element.classList.add("scene-title");
        let titleId = document.createElement("div");
        titleId.classList.add("scene-id");
        titleId.style.backgroundColor = "var(--" + role + "-color)";
        let titleIcon = document.createElement("i");
        titleIcon.classList.add(...(u.roles[role]["icon"].split(" ")));
        titleId.appendChild(titleIcon);
        let titleLabel = document.createElement("div");
        titleLabel.classList.add("scene-label");
        titleLabel.appendChild(document.createTextNode(u.roles[role]["label"]))

        element.appendChild(titleId);
        element.appendChild(titleLabel);

        return element;
    }

    /**
     * Returns a <div> element composed of a role title element and scene
     * elements to be displayed in the content menu.
     * @param {string} role map role of the div
     * @param {string} prefix menu prefix (ar/ls)
     * @param {object} scenes dictionary of scenes
     * @returns <div> element
     */
    function getSceneRoleDiv(role, prefix, scenes) {
        let sceneRoleDiv = document.createElement("div");
        sceneRoleDiv.id = prefix + "-" + role;
        sceneRoleDiv.classList.add(prefix, "me-1");
        sceneRoleDiv.appendChild(getSceneTitleElement(role));
        for (let id in scenes) {
            let scene = scenes[id];
            let e = getSceneElement(prefix, scene, sceneCounter);
            sceneCounter += 1;
            sceneRoleDiv.appendChild(e);
        }

        return sceneRoleDiv;
    }

    function printSceneRoleDiv(element, role, prefix, scenes) {
        let sceneRoleDiv = document.createElement("div");
        // sceneRoleDiv.id = prefix + "-" + role;
        // sceneRoleDiv.classList.add("d-flex");
        sceneRoleDiv.appendChild(getSceneTitleElement(role));
        let c = 2;
        for (let id in scenes) {
            let scene = scenes[id];
            let e = getSceneElement(prefix, scene, sceneCounter);
            sceneCounter += 1;
            if (c > 0) {
                sceneRoleDiv.appendChild(e);
                if (c == 1) { element.appendChild(sceneRoleDiv); }
                c -= 1;
            } else {
                element.appendChild(e);
            }

        }

        return sceneRoleDiv;
    }
    /**
     * Returns a <div> pointer element used on the content menu map.
     * @param {string} prefix menu prefix (ar/ls)
     * @param {string} id scene ID
     * @returns <div> element
     */
    function getMapPointerElement(prefix, id) {
        let scene = scenesData[id];
        let pointerElement = document.createElement("div");
        pointerElement.id = prefix + "-map-pointer-" + scene.id;
        pointerElement.classList.add("map-pointer")
        pointerElement.setAttribute("data-id", scene.id);
        pointerElement.style.top = "calc(" + scene["map-position"][0].toString() + "% - var(--offset))";
        pointerElement.style.left = "calc(" + scene["map-position"][1].toString() + "% - var(--offset))";
        pointerElement.style.backgroundColor = "var(--" + scene["map-role"] + "-color)";

        let txt = document.createElement("p");
        txt.appendChild(document.createTextNode(document.querySelector("#" + prefix + "-scene-" + scene.id).firstElementChild.textContent));
        pointerElement.appendChild(txt);

        return pointerElement;
    }


    /**
     * Prints the html content for the 'all rooms' content menu.
     */
    function printAllRooms() {
        let element = document.querySelector("#content-all-rooms");
        sceneCounter = 1;
        levelLabel = true;
        for (let role in u.roles) {
            if (scenesByRole[role] != null) {
                // element.appendChild(getSceneRoleDiv(role, "ar", scenesByRole[role]));
                printSceneRoleDiv(element, role, "ar", scenesByRole[role]);
            }
        }
    }

    /**
     * Prints the html content for the 'levels' content menu
     */
    function printLevelRooms() {
        let element = document.querySelector("#cmenu-content-axio-scenes");
        for (let level in u.levels) {
            let sceneLevelDiv = document.createElement("div");
            sceneLevelDiv.id = "axio-scenes-" + level;
            sceneLevelDiv.classList.add("axio-scenes", "inactive")
            let levelScenesByRoles = getScenesByAttribute(scenesByLevel[level], "map-role");
            sceneCounter = 1;
            levelLabel = false;
            for (let role in u.roles) {
                if (levelScenesByRoles[role] != null) {
                    sceneLevelDiv.appendChild(getSceneRoleDiv(role, "ls", levelScenesByRoles[role]));
                }
            }
            element.appendChild(sceneLevelDiv);
        }
    }

    function printMapRooms() {
        let element = document.querySelector("#cr-map-scenes");
        for (let level in u.levels) {
            let sceneLevelDiv = document.createElement("div");
            sceneLevelDiv.id = "map-scenes-" + level;
            sceneLevelDiv.classList.add("map-scenes", "d-flex", "flex-wrap", "inactive")
            let levelScenesByRoles = getScenesByAttribute(scenesByLevel[level], "map-role");
            sceneCounter = 1;
            levelLabel = false;
            for (let role in u.roles) {
                if (levelScenesByRoles[role] != null) {
                    for (let id in levelScenesByRoles[role]) {
                        let scene = levelScenesByRoles[role][id];
                        let e = getSceneElement("mr", scene, sceneCounter);
                        e.classList.add("flex-fill");
                        sceneCounter += 1;
                        sceneLevelDiv.appendChild(e);
                    }
                }
            }

            element.appendChild(sceneLevelDiv);
        }
    }

    /**
     * Prints the html content for the nav in the 'levels' menu.
     */
    function printAxioNav() {
        let element = document.querySelector("#cmenu-content-axio-nav");
        for (let level in u.levels) {
            let axioLevelDiv = document.createElement("div");
            axioLevelDiv.id = "axio-nav-" + level;
            axioLevelDiv.classList.add("btn", "axio-nav");
            axioLevelDiv.setAttribute("data-lvl", level);
            let txt = document.createElement("p");
            txt.appendChild(document.createTextNode(u.levels[level]["label"]));

            axioLevelDiv.appendChild(txt);
            element.appendChild(axioLevelDiv);
        }
    }

    /**
     * Prints the html content for the axio image and different masks in the 'levels' menu.
     */
    function printAxioImage() {
        let element = document.querySelector("#cmenu-content-axio-images");
        let axioCont = document.createElement("div");
        axioCont.classList.add("axio-cont");
        let axioImage = document.createElement("img");
        axioImage.classList.add("content-image", "axio-image");
        axioImage.src = u.imagePath + "axio/axio_all.jpg";
        axioCont.appendChild(axioImage);
        let axioMaskMap = document.createElement("img");
        axioMaskMap.id = "axio-mask-map";
        axioMaskMap.src = u.imagePath + "axio/axio_map.png";
        axioMaskMap.useMap = "#axio-image-map";
        let axioImageMap = document.createElement("map");
        axioImageMap.id = "axio-image-map";
        axioImageMap.name = "axio-image-map";

        for (let level in u.levels) {
            let axioMaskImg = document.createElement("img");
            axioMaskImg.id = "axio-mask-" + level;
            axioMaskImg.classList.add("content-image", "axio-mask", "inactive");
            axioMaskImg.src = u.imagePath + "axio/axio_" + level + ".png";
            axioMaskImg.setAttribute("data-lvl", level);
            axioCont.appendChild(axioMaskImg);

            let axioMaskArea = document.createElement("area");
            axioMaskArea.id = "axio-area-" + level;
            axioMaskArea.shape = "rect";
            if (window.matchMedia("(min-width: 768px)").matches) {
                axioMaskArea.coords = u.levels[level]["coords-dt"];
            } else {
                axioMaskArea.coords = u.levels[level]["coords-mb"];
            }
            axioMaskArea.href = "javascript:void(0)";
            axioMaskArea.setAttribute("data-lvl", level);
            axioImageMap.appendChild(axioMaskArea);
        }

        axioCont.appendChild(axioMaskMap);
        axioCont.appendChild(axioImageMap);
        element.appendChild(axioCont);
    }

    /**
     * Prints the html content for the map image.
     */
    function printMapImage() {
        let element = document.querySelector("#cr-map-image");
        for (let level in u.levels) {
            let mapImageDiv = document.createElement("div");
            mapImageDiv.id = "map-image-div-" + level;
            mapImageDiv.classList.add("map-image-div", "inactive");
            mapImageDiv.setAttribute("data-lvl", level);
            let mapImage = document.createElement("img");
            mapImage.classList.add("content-image", "map-image");
            mapImage.src = u.imagePath + "maps/" + level + "-map.png";

            mapImageDiv.appendChild(mapImage);
            element.appendChild(mapImageDiv);
        }
    }

    /**
     * Prints the html content of the map pointers on the map image.
     */
    function printMapPointers() {
        for (let level in u.levels) {
            let mapImageDiv = document.querySelector("#map-image-div-" + level);

            let arPointersDiv = document.createElement("div");
            arPointersDiv.id = "ar-pointers-div-" + level;
            arPointersDiv.classList.add("ar-pointers-div", "inactive");

            let lsPointersDiv = document.createElement("div");
            lsPointersDiv.id = "ls-pointers-div-" + level;
            lsPointersDiv.classList.add("ls-pointers-div", "inactive");

            let levelScenesByRoles = getScenesByAttribute(scenesByLevel[level], "map-role");

            for (let role in u.roles) {
                for (let sceneId in levelScenesByRoles[role]) {

                    let arPointer = getMapPointerElement("ar", sceneId);
                    arPointersDiv.appendChild(arPointer);

                    let lsPointer = getMapPointerElement("ls", sceneId);
                    lsPointersDiv.appendChild(lsPointer);
                }
            }

            mapImageDiv.appendChild(arPointersDiv);
            mapImageDiv.appendChild(lsPointersDiv);
        }
    }

    // Print the content menu
    printAllRooms();
    printLevelRooms();
    printAxioNav();
    printAxioImage();
    printMapRooms()
    printMapImage();
    printMapPointers();
};

export function link(mScenes) {

    /**
     * Sets the event listeners on the nav buttons of the 'levels' menu.
     */
    document.querySelectorAll(".axio-nav.btn, #axio-image-map>area").forEach((elem) => {

        let lvl = elem.getAttribute("data-lvl");
        elem.addEventListener("mouseenter", function (e) {
            // button
            document.querySelector("#axio-nav-" + lvl).classList.add("hover");
            // mask
            document.querySelector("#axio-mask-" + lvl).classList.add("hover");
            // scene list
            // map
        });

        elem.addEventListener("mouseleave", function (e) {
            // button
            document.querySelector("#axio-nav-" + lvl).classList.remove("hover");
            // mask
            document.querySelector("#axio-mask-" + lvl).classList.remove("hover");
            // scene list
            // map
        });

        elem.addEventListener("click", function (e) {
            // button
            u.setClassInList("#axio-nav-" + lvl, ".axio-nav", "active");
            // mask
            u.setClassInList("#axio-mask-" + lvl, ".axio-mask", "active", "inactive");
            // scene list
            u.setClassInList("#axio-scenes-" + lvl, ".axio-scenes", "active", "inactive");
            // map
            u.setClassInList("#map-image-div-" + lvl, ".map-image-div", "active", "inactive");
            if (window.matchMedia("(max-width: 768px)").matches) {
                // map scenes
                u.setClassInList("#map-scenes-" + lvl, ".map-scenes", "active", "inactive");
                //switch to map tab
                u.setClassInList("#button-map-mb", ".buttons-mb", "active");
                u.setClassInList("#content-right", "#content-right, #content-left", "active", "inactive");
                u.setClassInList(".ls-pointers-div", ".ar-pointers-div, .ls-pointers-div", "active", "inactive");

            }
        });
    });

    /**
     * Sets the event listeners on all the 'scene' elements.
     */
    document.querySelectorAll("#content-left .scene").forEach((elem) => {

        let id = elem.getAttribute("data-id");
        let lvl = elem.getAttribute("data-lvl");
        elem.addEventListener("mouseenter", function (e) {
            // scene
            elem.classList.add("hover");
            // map
            u.setClassInList("#map-image-div-" + lvl, ".map-image-div", "active", "inactive");
            // pointer - using elem.id.replace manages the ar/ls separation
            u.setClassInList("#" + elem.id.replace("scene", "map-pointer"), ".map-pointer", "hover");

        });
        elem.addEventListener("mouseleave", function (e) {
            // scene
            elem.classList.remove("hover");
            // map
            // TODO go back to current scene
            //pointer
            document.querySelector("#" + elem.id.replace("scene", "map-pointer")).classList.remove("hover");

        });

        elem.addEventListener("click", function (e) {
            // switch marzipano scene
            u.switchScene(mScenes, id);
            document.querySelector('#cmenu').classList.remove('show');
            document.querySelector('#ttb-cmenu-nav-button').classList.add('collapsed');
        });
    });

    /**
     * Sets the event listeners on all the mobile map 'scene' elements.
     */
    document.querySelectorAll("#content-right .scene").forEach((elem) => {

        let id = elem.getAttribute("data-id");
        let lvl = elem.getAttribute("data-lvl");
        elem.addEventListener("mouseenter", function (e) {
            // scene
            elem.classList.add("hover");
        });
        elem.addEventListener("mouseleave", function (e) {
            // scene
            elem.classList.remove("hover");

        });

        elem.addEventListener("click", function (e) {
            // switch marzipano scene
            u.switchScene(mScenes, id);
            document.querySelector('#cmenu').classList.remove('show');
            document.querySelector('#ttb-cmenu-nav-button').classList.add('collapsed');
        });
    });

    /**
     * Sets the event listeners on all the 'map pointers' elements.
     */
    document.querySelectorAll(".map-pointer").forEach((elem) => {
        let id = elem.getAttribute("data-id");

        elem.addEventListener("mouseenter", function (e) {
            //scene
            u.setClassInList("#" + elem.id.replace("map-pointer", "scene"), ".scene", "hover");
            //pointer
            elem.classList.add("hover");
        });

        elem.addEventListener("mouseleave", function (e) {
            //scene
            document.querySelector("#" + elem.id.replace("map-pointer", "scene")).classList.remove("hover");
            //pointer
            elem.classList.remove("hover");
        });

        elem.addEventListener("click", function (e) {
            // switch marzipano scene
            u.switchScene(mScenes, id);
            document.querySelector('#cmenu').classList.remove('show');
        });
    });

    /**
     * Sets the event listeners content menu all rooms / levels navigation.
     */
    document.querySelector("#button-all-rooms-dt").addEventListener("mouseup", function (e) {
        u.setClassInList("#button-all-rooms-dt", ".buttons-dt", "active");
        u.setClassInList("#content-all-rooms", ".content-dt", "active", "inactive");
        u.setClassInList(".ar-pointers-div", ".ar-pointers-div, .ls-pointers-div", "active", "inactive");

    });

    document.querySelector("#button-levels-dt").addEventListener("mouseup", function (e) {
        u.setClassInList("#button-levels-dt", ".buttons-dt", "active");
        u.setClassInList("#content-levels", ".content-dt", "active", "inactive");
        u.setClassInList(".ls-pointers-div", ".ar-pointers-div, .ls-pointers-div", "active", "inactive");

        u.setClassInList("#map-image-div-" + document.querySelector(".btn.axio-nav.active").getAttribute("data-lvl"), ".map-image-div", "active", "inactive");


    });

    document.querySelector("#button-all-rooms-mb").addEventListener("mouseup", function (e) {
        u.setClassInList("#button-all-rooms-mb", ".buttons-mb", "active");
        u.setClassInList("#content-left", "#content-right, #content-left", "active", "inactive");
        u.setClassInList("#content-all-rooms", ".content-mb", "active", "inactive");
        u.setClassInList(".ls-pointers-div", ".ar-pointers-div, .ls-pointers-div", "active", "inactive");
    });

    document.querySelector("#button-levels-mb").addEventListener("mouseup", function (e) {
        u.setClassInList("#button-levels-mb", ".buttons-mb", "active");
        u.setClassInList("#content-left", "#content-right, #content-left", "active", "inactive");
        u.setClassInList("#content-levels", ".content-mb", "active", "inactive");
        u.setClassInList(".ls-pointers-div", ".ar-pointers-div, .ls-pointers-div", "active", "inactive");
    });

    document.querySelector("#button-map-mb").addEventListener("mouseup", function (e) {
        u.setClassInList("#button-map-mb", ".buttons-mb", "active");
        u.setClassInList("#content-right", "#content-right, #content-left", "active", "inactive");
        u.setClassInList(".ls-pointers-div", ".ar-pointers-div, .ls-pointers-div", "active", "inactive");
    });

    // Startup
    // scene setup is done because we do a switchScene() at start

    u.setClassInList("#button-levels-dt", "#button-all-rooms-dt, #button-levels-dt", "active");
    u.setClassInList("#content-levels", "#content-all-rooms, #content-levels", "active", "inactive");
    u.setClassInList(".ls-pointers-div", ".ar-pointers-div, .ls-pointers-div", "active", "inactive");

};
